<template>
  <div :class="$root.themeHomeChange === '1' ? 'defect-table_light' : 'defect-table'" id="defect-table">
    <div class="header">
      <span>缺陷清单</span>
      <div style="font-size: 12px; font-weight: 600;">
        <span>缺陷状态： </span>
        <el-select
          v-model="params.defectStatus"
          size="mini"
          @change="getTableList(true)"
          clearable
          placeholder="请选择">
          <el-option
            v-for="item in defectStatusOptions"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
      </div>
    </div>
    <div class="content">
      <el-table
        ref="multipleTable"
        :data="tableData"
        style="width: 100%;"
        tooltip-effect="light"
        :height="tableHeight"
        @selection-change="handleSelectionChange"
        :row-key="rowKeyInit"
        v-loading="tableLoading"
        element-loading-text="拼命加载中"
        element-loading-spinner="el-icon-loading"
        element-loading-background="rgba(0, 0, 0, 0.8)"
      >
        <el-table-column
          type="selection"
          :selectable="selectInit"
          reserve-selection
          width="55"
          align="center">
        </el-table-column>
        <el-table-column
          type="index"
          label="序号"
          width="60"
          align="center"
        ></el-table-column>
        <el-table-column
          prop="ruleName"
          label="规则名称"
          min-width="150"
          align="center"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="ruleType"
          label="规则分类"
          width="100"
          align="center"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="department"
          label="部门"
          width="100"
          align="center"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="businessPhase"
          label="业务阶段"
          width="120"
          align="center"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="businessItem"
          label="业务事项"
          min-width="140"
          align="center"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="gzdy"
          label="规则定义"
          align="center"
          min-width="120"
          show-overflow-tooltip
        ></el-table-column>
        <el-table-column
          prop="riskLevel"
          label="风险等级"
          width="85"
          align="center"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="duty"
          label="责任岗位"
          width="105"
          align="center"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="wjtwsy"
          label="文件条文索引"
          min-width="180"
          align="center"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="defectStatus"
          label="异常详情"
          width="85"
          align="center"
          show-overflow-tooltip>
          <template slot-scope="scope">
            <!-- <span   slot-scope="scope"
              :style="{
                cursor: 'pointer',
                color: (!!scope.row.defectStatus ? '#67C23A' : '#E6A23C'),
                'font-weight': 'bold'
              }"
              @click="!!scope.row.defectStatus ? () => {} : detailDialogClick()">
              {{ !!scope.row.defectStatus ? "正常" : "异常" }}
            </span> -->
            <span
              style="cursor: pointer;
                     color: #E6A23C;
                     font-weight: bold"
              @click="detailDialogClick(scope.row)">
              异常
            </span>
          </template>
        </el-table-column>
        <el-table-column
          prop="defectStatus"
          label="缺陷状态"
          width="60"
          align="center"
          show-overflow-tooltip>
          <template slot-scope="scope">
            <span style="color: #49cfff; font-weight: bold;">{{
              defectStatusOptions.find(item => {
                return item.value == scope.row.defectStatus
              }).label || '错误阶段'
            }}</span>
          </template>
        </el-table-column>
        <el-table-column
          prop="beginTime"
          label="开始时间"
          min-width="180"
          align="center"
          show-overflow-tooltip>
          <template slot-scope="scope">
            {{ scope.row.beginTime || '暂无' }} 
          </template>
        </el-table-column>
        <el-table-column
          prop="updateTime"
          label="更新时间"
          min-width="180"
          align="center"
          show-overflow-tooltip>
          <template slot-scope="scope">
            {{ scope.row.updateTime || '暂无' }} 
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="params.currentPage"
        :page-sizes="[10, 50, 100, 200]"
        :page-size="params.pageSize"
        background
        layout="prev, pager, next, sizes, jumper, total"
        :total="params.total">
      </el-pagination>
    </div>
    <detail-dialog ref="detailDialogRef" :tableData="detailInfo"></detail-dialog>
  </div>
</template>

<script>
  import detailDialog from "../component/dialog/detailDialog";
  import Interface from "@/base/mixin/disposal-center.js";
  // import Qs from "qs";
  export default {
    name: "defect-table",
    components: { detailDialog },
    mixins: [Interface],
    props: {
      orderId: {
        type: String,
        default: () => {
          return ''
        }
      },
      taskId: {
        type: String,
        default: () => {
          return ''
        }
      },
      selectIds: {
        type: Array,
        default: () => {
          return []
        }
      },
    },
    data() {
      return {
        tableHeight: (window.innerHeight - 170) * 0.7 - 130,
        detailInfo: [], // 异常详细信息
        params: {
          pageSize: 10,
          currentPage: 1,
          total: 0
        },
        defectStatus: '',
        defectStatusOptions: [
          {
            value: 0,
            label:"待处理",
          },
          {
            value: 1,
            label:"待取证",
          },
          {
            value: 2,
            label:"待确认",
          },
          {
            value: 3,
            label:"待整改",
          },
          {
            value: 4,
            label:"已整改",
          },
          {
            value: 5,
            label:"已完成",
          },
          {
            value: 6,
            label:"已退回",
          },
          {
            value: 7,
            label:"待举证",
          },
          {
            value: 8,
            label:"举证中",
          },
          {
            value: 9,
            label:"已取消",
          }
        ],
        multipleSelection: [], // 多选
        tableLoading: false,
        tableData: []
      }
    },
    watch: {
      'selectIds': {
        handler(v) {
          window.console.log(v)
          if(Array.isArray(v) && v.length !== 0) {
            this.toggleSelection(v);
          }
        },
        deep: true,
        immediate: false
      },
      'taskId': {
        handler() {
          // window.console.log(v)
          this.getTableList();
        },
        deep: true,
        immediate: false
      },
    },
    created() {
      // this.initData();
    },
    mounted() {},
    methods: {
      // 初始化数据
      initData() {
        this.getTableList();
      },
      // row-key定义
      rowKeyInit(row) {
        return row.defectId;
      },
      // 获取table表格数据
      getTableList(reset) {
        if(reset) {
          this.setPages();
        }
        let params = {
          defectStatus: this.params.defectStatus,
          current: this.params.currentPage,
          size: this.params.pageSize,
          orderId: this.orderId,
          taskId: this.taskId
        };
        this.tableData = [];
        this.tableLoading = true;
        this.getDefectList(params).then((rs)=>{
          window.console.log(rs)
          if(rs) {
            this.tableData = rs.rows;
            this.setPages(rs.current, rs.size, rs.records);
          } else {
            this.setPages();
          }
        }).finally(()=>{
          this.tableLoading = false;
        })
      },
      toggleSelection(defectIds) {
        if(defectIds) {
          defectIds.forEach(id => {
            window.console.log(id, this.tableData)
            this.$refs.multipleTable.toggleRowSelection(
              this.tableData.find(item => {
                return item.defectId === id
              })
            );
          });
        } else {
          this.$refs.multipleTable.clearSelection();
        }
      },
      // 设置页码
      setPages(currentPage = 1, pageSize = 10, total = 0) {
        this.params['pageSize'] = pageSize;
        this.params['currentPage'] = currentPage;
        this.params['total'] = total;
      },
      // 搜索对应内容
      searchTableNames(arr) {
        let title = '';
        arr.forEach(item_1 => {
          title += this.tableData.find(item_2 => item_2.defectId == item_1.defectId).ruleName + ',';
        });
        return title;
      },
      handleSizeChange(val) {
        // window.console.log(`每页 ${val} 条`);
        this.params.pageSize = val;
        this.getTableList();
      },
      handleCurrentChange(val) {
        // window.console.log(`当前页: ${val}`);
        this.params.currentPage = val;
        this.getTableList();
      },
      // 查看异常详情
      detailDialogClick(row) {
        this.getMonitorException({id: row.id}).then((rs)=>{
          // window.console.log('异常详情', rs);
          if (Array.isArray(rs)) {
            this.detailInfo = rs;
          } else {
            this.detailInfo = [];
          }
        }).catch((err) => {
          this.detailInfo = [];
          this.$message.error('异常详情报错： ' + err.code);
        }).finally(()=>{
          this.$refs.detailDialogRef.openDialog()
        });
      },
      // 多选
      handleSelectionChange(val) {
        // window.console.log(val)
        this.multipleSelection = val;
        this.$emit('selectChange', val.map(item => {
          return {
            id: item.defectId,
            defectStatus: item.defectStatus,
          }
        }))
      },
      selectInit(row) {
        // console.log(row, index)
        let judge = true;
        if(this.multipleSelection.length != 0) {
          judge = this.multipleSelection.some(item => {
            if(item.defectStatus === row.defectStatus && row.defectStatus !== 5) {
              return true;
            }
          })
        }
        
        return judge;
      },
    }
  }
</script>

